import { useEffect, useState } from 'react';
import {useParams, useHistory} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size: 25px;
    color: ${ props=> props.색상}
`;


function Detail(props){
    
    let [com, chgCom] = useState(true);
    let [put, chgPut] = useState('');
    useEffect(()=>{
        let timer = setTimeout(()=>{ chgCom(false) }, 1000);
        console.log('ㅎ ㅏ ㅇ ㅣ 루 ~ *');
        return ()=>{ clearTimeout(timer)}
    },[]);

    let {any} = useParams();
    let history = useHistory();
    let 찾은상품 = props.shoes.find((슈즈의프로퍼티)=>{
        return 슈즈의프로퍼티.id == any
    })
    
    return(
        <div className="container">
        <Box>
            <제목 className="red">이야이야호 무야호~</제목>
            <제목 색상='green'>이야이야호 무야호~</제목>
        </Box>
        <input onChange={(e)=>{ chgPut(e.target.value)  }}/>
        {put}
        {
        com === true
        ? <ComAlarm/>
        : null
        }
        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}</p>
            <Info stock={props.stock}/>
            <button className="btn btn-danger" onClick={()=>{
                let newArr = [...props.stock]
                newArr = [33, 34, 45]
                props.재고변경(newArr)

            }}>주문하기</button> 
            <button className="btn btn-danger" onClick={ ()=>{
                // history.goBack();
                history.push('/')
            }}>뒤로가기</button> 
    
          </div>
        </div>
    
  </div> 
    )
}
let ComAlarm = (props)=>{
    return <div className="my-alarm2">
    <p>Out of stock soon !</p>
</div>
} 

let Info = (props)=>{
    return <p>재고: {props.stock} </p>   
}

export default Detail;